.main__nav {
  padding: 2rem 1rem 2rem;
  display: flex;
  justify-content: space-between;
  margin-bottom: 2rem;
  align-items: center;
  border-bottom: solid 1px #f2f2f2;
}

.main__nav-left {
  display: flex;
  align-items: center;

  @media (max-width: (499px)) {
    display: none;
  }
}

.main__nav-right {
  display: flex;
  align-items: center;

  @media (max-width: (991px)) {
    display: none;
  }
}

.main__nav-link {
  position: relative;

  &:hover {
    color: red;
  }
}

.main__nav-item {
  position: relative;
  color: $black;
  font-size: 1.8rem;
  margin-right: 2.5rem;

  &:last-child {
    margin-right: 0;
  }

  &--active {
    font-weight: 700;
    position: relative;
    
    & > .main__nav-link {
      position: relative;
      cursor:text;
      color: $black;
    }

    &::after {
      content: "";
      display: block;
      position: absolute;
      height: 2px;
      width: 100%;
      border-bottom: 2px solid #0096ff;
      bottom: -26px;
    }
  }

  @media (max-width: (991px)) {
    font-size: 1.7rem;
  }

  @media (max-width: 768px) {
    margin-right: 1rem;
    font-size: 1.5rem;
  }
}

.main__nav-word {
  font-size: 1.8rem;
  font-family: 'PT Sans';
  font-weight: 700;
  text-transform: uppercase;
  color: $black;
  margin-right: 3.5rem;

  &:hover {
    color: red;
  }
}

.main__nav-btn {
  background-color: #0095fc;
  border: none;
  padding: 0rem 1.5rem;
  border-radius: .5rem;
  font-size: 1.6rem;
  cursor: pointer;

  &:hover {
    background-color: #0077c9;
  }
}

.main__btn-link {
  color: $white;
  line-height: 2.8rem;
}

.main__nav-search {
  width: 18px;
  height: 18px;
  border: none;
  cursor: pointer;
  background: none;
}

.main__search-icon {
  position: relative;
  top: -1px;
  height: 18px;
  width: 18px;
}