.swiper-pagination {
  position: relative;

  @media (max-width: (1199px)) {
    display: none;
  }
}

.swiper-pagination-bullet {
  position: relative;
  background: none;
  border: 2px solid $white;
  opacity: 1;
  height: 12px;
  width: 12px;
}

.swiper-pagination-bullet-active {
  position: relative;
  z-index: 1000;
  background-color: #fff;
  opacity: 1;
}

.swiper-button-prev,
.swiper-button-next {
  position: absolute;
  border: none;
  background: none;
  outline: none;
}

.swiper-button-prev:after,
.swiper-button-next:after {
  display: none;
}

.swiper__buttons {
  display: flex;
  align-items: center;
}

.swiper-button-prev {
  background-image: url(../img/icons/right-arrow.png);
  background-repeat: no-repeat;
  width: 35px;
  height: 35px;
  transform: rotate(180deg);

  @media (max-width: (991px)) {
    margin-left: 7rem;
  }

  @media (max-width: (768px)) {
    margin: 0;
  }

  @media (max-width: (499px)) {
    left: 0;
    background-size: 25px 25px;
  }
}

.swiper-button-next {
  background-image: url(../img/icons/right-arrow.png);
  background-repeat: no-repeat;
  width: 35px;
  height: 35px;

  @media (max-width: (991px)) {
    margin-right: 7rem;
  }

  @media (max-width: (768px)) {
    margin: 0;
  }

  @media (max-width: (499px)) {
    right: 0;
    background-size: 25px 25px;
    margin-top: 9px;
  }
}

.swiper-container {
  padding-bottom: 50px;
}

.swiper-container-horizontal > .swiper-pagination-bullets,
.swiper-pagination-custom,
.swiper-pagination-fraction {
  bottom: 30px;
  left: 190px;
}

.slider__poster-wrapper {
  position: relative;
  display: flex;
  justify-content: center;
}

.slider__poster-info {
  position: absolute;
  top: 5px;
  left: 60px;
  width: 40%;

  @media (max-width: (991px)) {
    left: 100px;
    top: 0;
  }

  @media (max-width: (768px)) {
    left: 20px;
  }

  @media (max-width: (499px)) {
    top: -5px;
  }
}

.slider__poster-title {
  color: $white;
  font-size: 3rem;
  font-weight: 700;

  @media (max-width: (1199px)) {
    font-size: 2.5rem;
  }

  @media (max-width: (499px)) {
    font-size: 1.3rem;
    margin-bottom: -1rem;
  }
}

.slider__poster-desc {
  font-size: 1.8rem;
  color: $white;
  font-weight: 400;

  @media (max-width: (1199px)) {
    font-size: 1.5rem;
  }

  @media (max-width: (499px)) {
    font-size: 1.2rem;
  }
}

.slider__poster-bottom {
  display: flex;
  align-items: center;
  position: absolute;
  color: $white;
  bottom: 10px;
  left: 60px;

  @media (max-width: (991px)) {
    left: 100px;
    bottom: 0;
  }

  @media (max-width: (768px)) {
    left: 20px;
  }

  @media (max-width: (499px)) {
    bottom: -5px;
  }
}

.slider__poster-number {
  font-size: 3rem;
  font-family: 'PT Sans';
  font-weight: 700;
  color: $white;
  margin-right: 1rem;

  @media (max-width: (1199px)) {
    font-size: 2.5rem;
  }

  @media (max-width: (499px)) {
    font-size: 1.5rem;
  }
}

.slider__poster-mounth {
  font-weight: 700;
  font-size: 18px;
  color: $white;
  margin: 0;
	padding: 0;
  text-transform: lowercase;

  @media (max-width: (1199px)) {
    font-size: 1.5rem;
  }

  @media (max-width: (499px)) {
    font-size: 1.4rem;
  }
}

.slider__poster-day {
  font-family: 'PT Sans';
  font-weight: 400;
  font-size: 1rem;
  color: $white;
  text-transform: lowercase;
  margin: 0;
	padding: 0;

  @media (max-width: (499px)) {
    font-size: 0.9rem;
  }
}

.slider__poster-time {
  color: $white;
  font-size: 3rem;
  font-weight: 700;
  margin: 0 2.5rem;

  @media (max-width: (1199px)) {
    font-size: 2.5rem;
  }

  @media (max-width: (499px)) {
    font-size: 1.5rem;
  }
}

.slider__poster-link  {
  display: inline;
  padding: .8rem 1.3rem;
  background-color: #fff;
  color: $black;
  font-size: 1.6rem;
  border-radius: .5rem;

  &--type-1 {
    background-color: #ee721a;
    color: $white;
  }

  @media (max-width: (1199px)) {
    padding: .6rem 1.1rem;
    font-size: 1.3rem;
  }

  @media (max-width: (499px)) {
    padding: .3rem .8rem;
    font-size: 1.1rem;
  }
}