.header {
  background-color: $blue;
  height: 50px;
}

.header__link {
  margin-right: 0.5rem;
}

.header__link-logo {
  width: 54px;
  height: 29px;

  @media (max-width: (768px)) {
    position: relative;
    top: 10px;
  }
}

.header__nav {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: flex-start;
}

.header__nav-item {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 5rem;
  padding: 0rem .5rem;

  &--active {
    position: relative;
    
    &::before {
      position: absolute;
      content: "";
      display: block;
      width: 100%;
      height: 5px;
      background-color: $yellow;
      bottom: 0;
    }
  }

  @media (max-width: (768px)) {
    display: none;
  }
}

.header__nav-link {
  font-size: 1.5rem;
  color: $white;
  font-weight: 400;
  font-family: 'PT Sans';

  &:hover {
    text-decoration: underline;
  }
}

.header__nav-avatar {
  position: absolute;
  right: 0;

  @media (max-width: (768px)) {
    top: 10px;
  }
}

.header__avatar-icon {
  position: relative;
  z-index: 2;
  height: 30px;
  width: 30px;
}

.header__avatar-link {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;

  &::before {
    content: "";
    display: block;
    position: absolute;
    width: 40px;
    height: 40px;
    border-radius: 50%;
    background-color: #0083db;
    z-index: 1;
  }
}